import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user : null,
    isLoggedIn : false
  },
  mutations: {
    setUser(state,user){
      state.user = user
    },
    setLoggedIn : function(state){
      state.isLoggedIn = !state.isLoggedIn
    }
  },
  actions: {
  },
  modules: {
  }
})
